<script>
import area from "@U/area";
import {inputInviteCode, applyVip, applyVipDetail, getInviteInfo, getPromoteHome} from "@API/user"
export default {
    name: "userPromote",
    data() {
        return {
            showLoading: true,
            // 邀请状态
            inviteStatus: false,
            // 用户邀请码
            distributionCode: "",
            // 推销状态 0 ==> 未申请 1 ==> 申请进度 2 ==> 已申请
            vipState: 0,
            areaList: area,
            showArea: false,
            showPop: false,
            userInfo: {
                user: {}
            },
            // 审核信息对象
            applyObject: {},
            // 邀请码
            inviteCode: '',
            // 真实姓名
            realName: '',
            // 手机号码
            mobile: '',
            // 申请原因
            reason: '',
            // 地址
            address: '',
            // 省id
            provinceID: -1,
            // 市id
            cityID: -1,
            // 区ID
			districtID: -1,
        }
    },
    methods: {
        onCopy(str) {
            let aux = document.createElement("input");
            aux.setAttribute("value", str);
            document.body.appendChild(aux);
            aux.select();
            document.execCommand("copy");
            document.body.removeChild(aux);
            this.$toast({
                type: "success",
                message: "复制成功",
            });
        },
        openInvitePop() {
            this.showPop = true
        },
        onConfirm(e) {
            this.provinceID = e[0].code;
            this.cityID = e[1].code;
            this.districtID = e[2].code;
            this.address = e[0].name + ' ' + e[1].name + ' ' + e[2].name;
            this.showArea = false;
        },
        // 输入邀请码
        $inputInviteCode() {
            if(!this.inviteCode) {
                this.$toast("请输入邀请码")
                return;
            }
            inputInviteCode({
                code: this.inviteCode
            }).then(res => {
                if(res.code == 1) {
                    this.$toast(res.msg)
                }
            })
        },
        // 会员申请
        $applyVip() {
            if(!this.realName) {
                this.$toast("请填写真实姓名")
                return;
            }
            if(!this.mobile) {
                this.$toast("请填写手机号信息");
                return;
            }
            if(!this.address) {
                this.$toast("请选择省市区");
                return;
            }
            applyVip({
                real_name: this.realName,
                mobile: this.mobile,
                province: this.provinceID,
                city: this.cityID,
                district: this.districtID,
                reason: this.reason
            }).then(res => {
                if(res.code == 1) {
                    this.$applyVipDetail();
                }
            })
        },
        // 会员申请详情
        $applyVipDetail() {
            applyVipDetail().then(res => {
                if(res.code == 1) {
                    
                    if(Array.isArray(res.data)) {
                        if(res.data.length <= 0) {
                            this.vipState = 0
                        }
                    }
                    else {
                        switch(res.data.status) {
                            case 0:
                                // 待审核
                            case 2:
                                // 审核不通过
                                this.vipState = 1;
                                this.applyObject = res.data;
                                break;
                            case 1:
                                // 审核通过
                                this.vipState = 2;
                                this.applyObject = res.data;
                                this.$getPromoteHome();
                                break;
                        }
                    }
                    if(this.showLoading == true) {
                        this.$toast.clear();
                        this.showLoading = false;
                    }
                }
            })
        },
        // 邀请人信息
        $getInviteInfo() {
            getInviteInfo().then(res => {
                if(res.code == 1) {
                    this.userInfo = res.data;
                    if(this.userInfo.leader.length <= 0) {
                        this.inviteStatus = false;
                    } else {
                        this.inviteStatus = true;
                    }
                }
            })
        },
        // 获取分销首页数据
        $getPromoteHome() {
            getPromoteHome().then(res => {
                if(res.code == 1) {
                    this.userInfo = res.data;
                    // 设置状态
                    if(this.userInfo.leader.length <= 0) {
                        this.inviteStatus = false;
                    } else {
                        this.inviteStatus = true;
                    }
                }
            })
        },
        onApply() {
            this.$applyVip();
        }
    },
    created() {
        if(this.showLoading == true) {
            this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
            });
        }
        this.$getInviteInfo();
        this.$applyVipDetail()
    }
}
</script>

